<template>
  <div class="create-report">
    <div class="page-header mb-6">
      <h1 class="text-2xl font-bold text-gray-900">Buat Laporan Baru</h1>
      <p class="text-gray-600">Laporkan dugaan pelanggaran dengan aman dan terjamin</p>
    </div>
    
    <div class="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
      <form @submit.prevent="submitReport" class="space-y-6">
        <!-- Report Name -->
        <div>
          <label for="name" class="block text-sm font-medium text-gray-700 mb-2">
            Nama Laporan <span class="text-red-500">*</span>
          </label>
          <input
            v-model="reportForm.name"
            type="text"
            id="name"
            placeholder="Masukkan nama laporan"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            required
          />
        </div>

        <!-- Time -->
        <div>
          <label for="time" class="block text-sm font-medium text-gray-700 mb-2">
            Waktu Kejadian <span class="text-red-500">*</span>
          </label>
          <input
            v-model="reportForm.time"
            type="datetime-local"
            id="time"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            required
          />
        </div>

        <!-- Place -->
        <div>
          <label for="place" class="block text-sm font-medium text-gray-700 mb-2">
            Tempat Kejadian <span class="text-red-500">*</span>
          </label>
          <input
            v-model="reportForm.place"
            type="text"
            id="place"
            placeholder="Masukkan lokasi kejadian"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            required
          />
        </div>

        <!-- Category -->
        <div>
          <label for="category" class="block text-sm font-medium text-gray-700 mb-2">
            Kategori <span class="text-red-500">*</span>
          </label>
          <select
            v-model="reportForm.category"
            id="category"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            required
          >
            <option value="">Pilih kategori</option>
            <option value="Security">Security</option>
            <option value="Financial">Financial</option>
            <option value="Operational">Operational</option>
            <option value="HR">Human Resources</option>
            <option value="Compliance">Compliance</option>
            <option value="IT">Information Technology</option>
            <option value="Other">Lainnya</option>
          </select>
        </div>

        <!-- Description -->
        <div>
          <label for="description" class="block text-sm font-medium text-gray-700 mb-2">
            Deskripsi Kejadian <span class="text-red-500">*</span>
          </label>
          <textarea
            v-model="reportForm.description"
            id="description"
            rows="4"
            placeholder="Jelaskan detail kejadian yang dilaporkan"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            required
          ></textarea>
        </div>

        <!-- Additional Information -->
        <div>
          <label for="information" class="block text-sm font-medium text-gray-700 mb-2">
            Informasi Tambahan
          </label>
          <textarea
            v-model="reportForm.information"
            id="information"
            rows="3"
            placeholder="Informasi tambahan atau detail yang relevan"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          ></textarea>
        </div>

        <!-- Item/Evidence -->
        <div>
          <label for="item" class="block text-sm font-medium text-gray-700 mb-2">
            Item/Barang Bukti
          </label>
          <input
            v-model="reportForm.item"
            type="text"
            id="item"
            placeholder="Sebutkan barang bukti yang terkait"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          />
        </div>

        <!-- Status (Hidden, will be set automatically) -->
        <input type="hidden" v-model="reportForm.status" />

        <!-- Submit Button -->
        <div class="flex justify-end space-x-4">
          <button
            type="button"
            @click="resetForm"
            class="px-6 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            Reset
          </button>
          <button
            type="submit"
            :disabled="isSubmitting"
            class="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <Icon v-if="isSubmitting" icon="mdi:loading" class="animate-spin mr-2" />
            {{ isSubmitting ? 'Mengirim...' : 'Kirim Laporan' }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue2";
import axios from 'axios';

export default {
  name: 'CreateReport',
  components: { Icon },
  
  data() {
    return {
      isSubmitting: false,
      reportForm: {
        name: '',
        time: '',
        place: '',
        category: '',
        description: '',
        information: '',
        item: '',
        status: 'Open'
      }
    }
  },

  mounted() {
    // Set default time to current datetime
    this.setCurrentDateTime();
  },

  methods: {
    setCurrentDateTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      const hours = String(now.getHours()).padStart(2, '0');
      const minutes = String(now.getMinutes()).padStart(2, '0');
      
      this.reportForm.time = `${year}-${month}-${day}T${hours}:${minutes}`;
    },

    formatTimeForAPI(datetimeLocal) {
      // Convert datetime-local format to API format (YYYY-MM-DD HH:mm:ss)
      const date = new Date(datetimeLocal);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');
      
      return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    },

    async submitReport() {
      try {
        this.isSubmitting = true;

        // Get token from localStorage
        const token = localStorage.getItem('token');
        if (!token) {
          this.$message.error('Sesi telah berakhir. Silakan login ulang');
          this.$router.push('/login');
          return;
        }

        // Prepare data for API
        const reportData = {
          name: this.reportForm.name,
          time: this.formatTimeForAPI(this.reportForm.time),
          place: this.reportForm.place,
          category: this.reportForm.category,
          description: this.reportForm.description,
          information: this.reportForm.information || '',
          item: this.reportForm.item || '',
          status: this.reportForm.status
        };

        console.log('Submitting report:', reportData);

        // API Call
        const response = await axios.post('http://localhost:8080/api/reports', reportData, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        });

        console.log('Report submission response:', response);

        // Success handling
        this.$message.success('Laporan berhasil dikirim!');
        this.resetForm();
        
        // Optional: Redirect to reports list
        // this.$router.push('/dashboard/reports');

      } catch (error) {
        console.error('Error submitting report:', error);
        
        let errorMessage = 'Gagal mengirim laporan';
        
        if (error.response) {
          const status = error.response.status;
          if (status === 401) {
            errorMessage = 'Sesi telah berakhir. Silakan login ulang';
            this.$router.push('/login');
          } else if (status === 403) {
            errorMessage = 'Akses ditolak untuk mengirim laporan';
          } else if (status === 400) {
            errorMessage = error.response.data?.message || 'Data laporan tidak valid';
          } else if (status === 422) {
            errorMessage = 'Data laporan tidak lengkap atau format salah';
          } else {
            errorMessage = error.response.data?.message || `Server error: ${status}`;
          }
        } else if (error.request) {
          errorMessage = 'Tidak dapat terhubung ke server. Periksa koneksi internet Anda';
        } else {
          errorMessage = error.message || 'Terjadi kesalahan saat mengirim laporan';
        }
        
        this.$message.error(errorMessage);
        
      } finally {
        this.isSubmitting = false;
      }
    },

    resetForm() {
      this.reportForm = {
        name: '',
        time: '',
        place: '',
        category: '',
        description: '',
        information: '',
        item: '',
        status: 'Open'
      };
      this.setCurrentDateTime();
    },

    validateForm() {
      const required = ['name', 'time', 'place', 'category', 'description'];
      for (let field of required) {
        if (!this.reportForm[field]) {
          this.$message.error(`${field} harus diisi`);
          return false;
        }
      }
      return true;
    }
  }
}
</script>

